video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}/*this plays with the video size*/

.hero-container{ /*video setting as well
    background: url('https://assets.mixkit.co/videos/preview/mixkit-hands-giving-a-relaxing-back-massage-14599-large.mp4') center center/cover no-repeat;*/
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: insert 0 0 0 1000px rgba(0,0,0,0.2);
    object-fit: contain;
}
.hero-container > h1 {
    color: #fff;
    font-size: 100px;
    margin-top: -100px;
}

.hero-container.p > {
    margin-top: 8px;
    color: #fff;
    font-size: 100%;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.hero-btns {
    margin-top: 32px;
}
.hero-btns {
    margin: 6px;
}
.fa-play-circle{
    margin-left: 4px;
}

@media screen and (max-width:960px)
 {
    .hero-container >h1{
        font-size: 70px;
        margin-top:-150;
    }
 }

 @media screen and (max-width: 768px) {
    .hero-container > h1{
        font-size: 50px;
        margin-top: -100px;
    }
    .hero-container >p{
        font-size: 30px;
    }
    .btn-mobile {
        display: block;
        text-decoration: none;
    }
    .btn {
        width: 100%;
    }
 }
